*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: "digital";
  src: local("GoldmanBold"),
   url("./fonts/arcadeclassic/ARCADECLASSIC.TTF") format("truetype");
  font-weight: bold;
  }
  

body{
  font-family: 'Courier New', Courier, monospace;
}

#drum-container{
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 300px;
  min-height: 200px;
  margin: 30vh auto;
  padding: 1rem;
  background-color: #a5865c;
  box-shadow: 0px 0px 10px black;
  border-radius: 8px;

  /* padding: 1rem; */
}


#display{
  color: white;
  
  font-size: 32px;
  font-family: "digital";
  
  margin: 0 auto;
  padding: 1rem;
  
  width: 225px;
  height: 60px;
  
  background: #616038;
  border: 6px solid #3B3315;
  border-radius: 8px;
}

#drum-machine{
  padding: 1rem;
  align-content: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem 1rem;
  
  
}

.drum-pad{
  background-color: #3b3315;
  color:aliceblue;
  font-weight: bolder;
  height: 50px;
  width: 50px;
  border: 5px solid #616038;
  border-radius: 5px;


}

.drum-pad:hover{
  cursor: pointer;
  background-color: #ffc800;
  box-shadow: 0px 0px 20px #ffc800;
  border: 5px solid #8a8947;
  transition: all 0.3s;

}

